.sp-view {
  width: 960px;
  height: 100%;
  margin: 0 auto;
}
.sp-view .ant-form-item-label {
  text-align: left;
}
.sp-view .ant-form-item-label > label::after {
  display: none !important;
}
.sp-view .ant-form-item-label > label {
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: justify;
  -moz-text-align-last: justify;
       text-align-last: justify;
  padding-right: 10px;
}
.sp-view .ant-input {
  border-radius: 0px   !important;
}
.sp-view .ant-input.changed {
  border-color: red;
}
.sp-view .ant-btn {
  border-radius: 0px   !important;
}
.sp-view .ant-select-selection {
  border-radius: 0px   !important;
}
.sp-view .ant-select-selection.changed {
  border-color: red;
}
.sp-view .ant-table-content > .ant-table-body {
  margin: 0 !important;
}
